.typo-content{    
    padding-Top: 4.5vh;
    padding-Left: 8%;
    padding-Right: 8%;
    padding-Bottom: 6vh;
    min-Height: 280px;
    max-width: 1080px;
    background-color: #e6f4ff;     
}

.login-content{    
    padding-Top: 4.5vh;
    padding-Left: 2%;
    padding-Right: 2%;
    padding-Bottom: 6vh;
    min-Height: 280px;
    max-width: 1080px;
    background-color: #e6f4ff;     
}

.login-div{
    margin-top: 3vh;
    padding-Top: 1vh;
    padding-Left: 0;
    padding-Right: 0;
    padding-Bottom: 2vh;
    height: auto;
    width: "100%";
    background-color: #d0eeff;     
    border-radius: 20px;
}

.search-content{    
    padding-Top: 2vh;
    padding-Left: 2%;
    padding-Right: 2%;
    padding-Bottom: 4vh;
    min-Height: 280px;
    width: 95%;
    max-width: 900px;
    background-color: #def0ff;     
}

.mix-content{    
    padding-Top: 4.5vh;
    padding-Left: 2%;
    padding-Right: 2%;
    padding-Bottom: 6vh;
    min-Height: 280px;
    width: 95%;
    max-width: 680px;
    background-color: #e5f0ff; 
}

.room-content{    
    padding-Top: 0;
    padding-Left: 2%;
    padding-Right: 2%;
    padding-Bottom: 0;
    width: 99%;
    max-width: 1500px;
    background-color: #e6f4ff; 
    display: flex;
    justify-content: center;
    align-items: top;   
}

.room:hover{
    box-shadow: 10px 5px 5px #efdbff;
    filter: brightness(1.02);
}